.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 10;
  background-color: #231f20;
  padding: 10px 0;
  transition: all 450ms cubic-bezier(0.075, 0.82, 0.165, 1);
  z-index: 8;
}

.header.header-hidden {
  transform: translateX(-1000px);
  width: 100%;
}

.header.header-hidden + .burger {
  opacity: 0;
  visibility: hidden;
}

.header.header-hidden.header-in-hero {
  transform: translateX(0);
  position: absolute;
  display: flex;
  justify-content: flex-end;
  background-color: transparent;
  margin-top: 40px;
  z-index: 8;
}

.header.header-hidden.header-in-hero .header__logo,
.header.header-hidden.header-in-hero .header__covid {
  display: none;
}

.header.header-hidden.header-in-hero .header__container {
  display: block;
  width: 58%;
}

.header.header-hidden.header-in-hero .container {
  margin: 0 0;
}

.header.header-hidden.header-in-hero .header__inner {
  justify-content: space-between;
}

.header.header-hidden.header-in-hero .header__btn {
  margin-top: -70px;
}

.header.header-hidden.header-in-hero .header__points-list {
  left: 130px;
  top: -29px;
}

.header.header-hidden.header-in-hero .header__phone {
  margin-top: 20px;
}

.header.header-hidden.header-in-hero + .burger {
  opacity: 1;
  visibility: visible;
  position: absolute;
  top: 50px;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__inner {
  display: flex;
  align-items: center;
  position: relative;
}

.header__phone {
  font-weight: 200;
  font-size: 1.8rem;
  transition: color 200ms ease-in-out;
  background: linear-gradient(to right, #17b18a, #1eb43e 50%, white 50%);
  display: inline-block;
  overflow: hidden;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 275ms ease;
}

.header__phone-black {
  background: linear-gradient(to right, #17b18a, #1eb43e 50%, black 50%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
}

.header__phone:hover {
  background-position: 0 100%;
}

.header__phone-icon {
  display: none;
}

.header__logo {
  margin-right: 40px;
}

.header__btn {
  margin-right: 130px;
}

.header__covid {
  display: flex;
  align-items: center;
  margin: 0 30px;
  color: #fff;
}

.header__covid img {
  margin-right: 10px;
}

.header__points {
  position: relative;
  padding: 13px 40px 13px 10px;
  background-color: transparent;
  border: 2px solid #1eb43e;
  border-radius: 10px;
  color: #fff;
  font-size: 1.3rem;
  font-weight: 300;
  cursor: pointer;
  text-transform: uppercase;
}

.header__points:hover {
  background-color: #1eb43e;
}

.header__points::after {
  content: "â–¼";
  font-size: 1.2rem;
  position: absolute;
  right: 13px;
  top: 14px;
  z-index: 5;
  color: #fff;
  pointer-events: none;
}

.header__points-list {
  transform: scale(0);
  transition: all 0.2s ease-in-out;
  background-color: #fff;
  border-radius: 10px;
  padding: 5px 15px;
  position: absolute;
  left: 30px;
  top: 40px;
  color: #494949;
  box-shadow: 0 0 15px 0 rgba(49, 49, 49, 0.822);
}

.header__points-list li {
  margin: 4px;
  cursor: pointer;
  padding: 3px;
  position: relative;
  z-index: 11;
}

.header__points-list li:hover {
  color: #1eb43e;
}

.header__points-list.active {
  transform: scale(1);
}

.btn {
  padding: 13px 22px 12px;
  background-color: #1eb43e;
  border: 2px solid #1eb43e;
  border-radius: 30px;
  color: #fff;
  font-size: 1.3rem;
  font-weight: 300;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}

.btn:hover {
  background-color: transparent;
}

.burger {
  position: fixed;
  width: 40px;
  height: 50px;
  right: 75px;
  top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: all 200ms ease-in-out;
}

.burger:hover .burger__item,
.burger:hover .burger__item::before,
.burger:hover .burger__item::after {
  background-color: #17882f;
}

.burger__item {
  position: relative;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
  transition: transform 200ms ease;
  pointer-events: none;
}

.burger__item::before {
  content: "";
  position: absolute;
  top: 10px;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
}

.burger__item::after {
  content: "";
  position: absolute;
  top: -10px;
  width: 100%;
  height: 4px;
  background-color: #1eb43e;
  border-radius: 5px;
}

.burger.close .burger__item {
  transform: rotate(-45deg);
}

.burger.close .burger__item:after {
  transform: rotate(90deg);
  top: 0;
}

.burger.close .burger__item::before {
  display: none;
}

.menu {
  position: fixed;
  transform: translateX(5000px);
  transition: transform 300ms ease-in-out;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #0c0c0c;
  z-index: 9;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.menu__list {
  margin-right: 150px;
}

.menu__list li a {
  font-size: 5rem;
  font-weight: 900;
  background: linear-gradient(to right, #0d9271, #1eb43e 50%, white 50%);
  display: inline-block;
  overflow: hidden;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 275ms ease;
}

.menu__list li a:hover {
  background-position: 0 100%;
}

.menu__list li .btn {
  margin-top: 30px;
}

.menu__btn {
  background-color: transparent;
}

.menu__btn:hover {
  background-color: #1eb43e;
}

.menu__social {
  display: flex;
  align-items: baseline;
  margin-top: 20px;
}

.menu__social li:first-child {
  position: relative;
  top: 5px;
}

.menu__social li {
  margin-right: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.menu__social li a {
  transition: all 200ms ease-in-out;
}

.menu__social li:hover a svg {
  fill: #fff;
}

.menu.active {
  transform: translateX(0);
}

.hero {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero__video {
  position: absolute;
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.hero__info {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.hero__text {
  color: #fff;
  width: 50%;
}

.hero__text h1 {
  font-size: 6rem;
  font-weight: 900;
  line-height: 80px;
  text-transform: uppercase;
  position: relative;
}

.hero__text h1::before {
  content: "";
  width: 60px;
  height: 5px;
  background-color: #1eb43e;
  position: absolute;
  bottom: -20px;
}

.hero__text p {
  font-size: 1.8rem;
  line-height: 20px;
  font-weight: 300;
}

.about {
  text-align: center;
  padding-top: 60px;
  max-width: 1200px;
  margin: 0 auto;
}

.about .title {
  margin-bottom: 10px;
}

.about__numbers {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 900px;
  margin: 80px auto;
}

.about__numbers li:first-child div {
  width: 225px;
}

.about__numbers li {
  display: flex;
  align-items: center;
  flex-grow: 1;
  margin-right: 10px;
}

.about__numbers li div {
  font-size: 4rem;
  font-weight: 700;
  white-space: nowrap;
  width: 100px;
  background: linear-gradient(to right, #07644d, #1eb43e 50%, white 50%);
  display: inline-block;
  overflow: hidden;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 0%;
  transition: background-position 275ms ease;
}

.about__numbers li p {
  color: #8a8a8a;
  font-size: 1.7rem;
  text-transform: uppercase;
  text-align: left;
  position: relative;
  margin-left: 12px;
  min-width: 150px;
}

.about__numbers li p::before {
  position: absolute;
  content: "";
  left: -8px;
  background-color: #8a8a8a;
  height: 100%;
  width: 1px;
}

.price__title-head {
  font-size: 4rem;
  font-weight: 800;
  margin: 0;
  margin-bottom: 25px;
}

.price__wrapper {
  display: flex;
  justify-content: space-between;
}

.price__services {
  display: flex;
  justify-content: space-between;
  flex-basis: 70%;
  border-top: 1px dashed #747474;
  padding-top: 13px;
}

.price__table {
  margin-top: 8px;
}

.price__column {
  flex-basis: 50%;
}

.price__column:not(:last-child) {
  margin-right: 20px;
}

.price__title {
  font-size: 2.2rem;
  display: inline-block;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

.price__title .price__discount {
  color: #1eb43e;
}

.price__title .price__card {
  font-size: 1.4rem;
  font-weight: 300;
  position: relative;
  padding-right: 33px;
}

.price__title .price__card:before {
  content: "";
  background-image: url("../img/icons/card-icon.svg");
  background-repeat: no-repeat;
  width: 25px;
  height: 20px;
  position: absolute;
  right: 0;
  z-index: 2;
}

.price__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px dashed #747474;
  cursor: pointer;
  transition: all 200ms ease-in-out;
}

.price__item.active .price__name {
  background-color: #1eb43e;
  color: #fff;
}

.price__item:hover .price__name {
  background-color: #1eb43e;
  color: #fff;
}

.price__item-no-hover {
  cursor: auto;
  pointer-events: none;
}

.price__item-no-hover:hover .price__name {
  background-color: transparent;
  color: #000;
}

.price__condition {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

.price__condition img {
  margin-right: 15px;
}

.price__name {
  display: inline-block;
  font-weight: 300;
  font-size: 1.4rem;
  max-width: 60%;
  margin-right: 10px;
  padding: 7px 5px;
  border-radius: 5px;
}

.price__time {
  font-size: 1.3rem;
  color: #747474;
  white-space: nowrap;
}

.price__info {
  display: flex;
  align-items: center;
}

.price__price {
  font-weight: 700;
  margin-left: 5px;
  white-space: nowrap;
}

.price__price-old {
  color: #1eb43e;
  text-decoration: line-through;
  font-size: 1.4rem;
}

.price__cond {
  font-size: 1.3rem;
  color: #747474;
  white-space: nowrap;
  margin-left: 5px;
}

.description {
  border-radius: 8px;
  box-shadow: 0 4px 24px 0 rgba(0, 0, 0, 0.13);
  margin-left: 25px;
  flex-basis: 30%;
}

.description img {
  width: 100%;
  display: block;
}

.description__title {
  font-size: 2rem;
  font-weight: 800;
  margin-top: 5px;
  padding: 7px 10px;
}

.description__text {
  padding: 0 10px;
  line-height: 24px;
  color: #454545;
  font-size: 1.5rem;
  height: 250px;
  overflow: scroll;
  padding-bottom: 10px;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1eb43e;
  visibility: visible;
  opacity: 1;
}

.preloader.preloader_hidden {
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s, visibility 0s 1s;
}

.spinner {
  width: 50px;
  height: 50px;
  position: relative;
  margin: 100px auto;
}

.double-bounce1,
.double-bounce2,
.double-bounce3 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #dadada;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: sk-bounce 2s infinite ease-in-out;
  animation: sk-bounce 2s infinite ease-in-out;
}

.double-bounce3 {
  background-color: #757575;
}

.double-bounce2 {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}

.double-bounce3 {
  -webkit-animation-delay: -2s;
  animation-delay: -2s;
}

@-webkit-keyframes sk-bounce {
  0%, 100% {
    -webkit-transform: scale(0);
  }

  50% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

.popup {
  background-color: #fff;
  width: 410px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 20;
  border-radius: 25px;
  opacity: 0;
  visibility: hidden;
}

.popup__overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background-color: rgba(2, 2, 2, 0.959);
  opacity: 0;
  visibility: hidden;
}

.popup__close {
  position: absolute;
  top: 15px;
  right: 15px;
